import {
  createContext,
  useContext,
  useState,
  useEffect,
  useCallback,
} from "react";

const ProductsContext = createContext();

export const useProducts = () => {
  return useContext(ProductsContext);
};

export const ProductsProvider = ({ children }) => {
  const [list, setList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [category, setCategory] = useState("todos");
  const [search, setSearch] = useState("");
  const [size, setSize] = useState("");
  const [availableSizes, setAvailableSizes] = useState([]);
  const [pagination, setPagination] = useState({
    page: 1,
    total: 0,
    totalPages: 1,
    limit: 12,
  });

  const fetchProducts = useCallback(
    async (page = 1, limit = 12) => {
      setLoading(true);
      try {
        const categoryFilter = category === "todos" ? "" : category;
        const response = await fetch(
          `${
            import.meta.env.VITE_BACKEND_URL
          }/api/products?page=${page}&limit=${limit}&category=${categoryFilter}&search=${search}&size=${size}`
        );
        if (!response.ok) {
          throw new Error("Error al obtener los productos");
        }
        const data = await response.json();
        setList(data.products);
        setPagination({
          page: data.page,
          total: data.total,
          totalPages: data.totalPages,
          limit: data.limit,
        });
      } catch (error) {
        setError(error.message);
        console.error("Error fetching products:", error);
      } finally {
        setLoading(false);
      }
    },
    [category, search, size]
  );

  useEffect(() => {
    const fetchSizes = async () => {
      try {
        const response = await fetch(
          `${import.meta.env.VITE_BACKEND_URL}/api/products/sizes`
        );
        if (!response.ok) {
          throw new Error("Error al obtener los talles");
        }
        const data = await response.json();
        setAvailableSizes(data);
      } catch (error) {
        console.error(error.message);
      }
    };
    fetchSizes();
  }, []);

  useEffect(() => {
    fetchProducts();
  }, [fetchProducts]);

  const getProductBySku = useCallback(async (sku) => {
    setLoading(true);
    try {
      const response = await fetch(
        `${import.meta.env.VITE_BACKEND_URL}/api/products/${sku}`
      );
      if (!response.ok) {
        if (response.status === 404) {
          return null; // Producto no encontrado
        }
        throw new Error("Error al obtener el producto");
      }
      return await response.json();
    } catch (error) {
      setError(error.message);
      console.error(`Error fetching product with sku ${sku}:`, error);
      return null;
    } finally {
      setLoading(false);
    }
  }, []);

  const createProduct = useCallback(async (productData) => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(`${import.meta.env.VITE_BACKEND_URL}/api/products`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(productData),
      });
      if (!response.ok) {
        const errData = await response.json();
        throw new Error(errData.error || 'Error al crear el producto');
      }
      await fetchProducts(); // Refrescar la lista de productos
    } catch (error) {
      setError(error.message);
      throw error; // Re-lanzar el error para que el formulario lo capture
    } finally {
      setLoading(false);
    }
  }, [fetchProducts]);

  const updateProduct = useCallback(async (sku, productData) => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(`${import.meta.env.VITE_BACKEND_URL}/api/products/${sku}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(productData),
      });
      if (!response.ok) {
        const errData = await response.json();
        throw new Error(errData.error || 'Error al actualizar el producto');
      }
      await fetchProducts(); // Refrescar la lista de productos
    } catch (error) {
      setError(error.message);
      throw error; // Re-lanzar el error para que el formulario lo capture
    } finally {
      setLoading(false);
    }
  }, [fetchProducts]);

  const clearError = () => {
    setError(null);
  };

  const fetchAllProducts = useCallback(async () => {
    setLoading(true);
    try {
      // Hacemos una peticin con un lmite muy alto para traer todos los productos
      const response = await fetch(
        `${import.meta.env.VITE_BACKEND_URL}/api/products?page=1&limit=9999`
      );
      if (!response.ok) {
        throw new Error("Error al obtener todos los productos");
      }
      const data = await response.json();
      setList(data.products); // Actualizamos la lista directamente
    } catch (error) {
      setError(error.message);
      console.error("Error fetching all products:", error);
    } finally {
      setLoading(false);
    }
  }, []); // No necesita dependencias, siempre hace lo mismo

  const value = {
    list,
    loading,
    error,
    category,
    setCategory,
    search,
    setSearch,
    size,
    setSize,
    availableSizes,
    pagination,
    fetchProducts,
    getProductBySku,
    createProduct,
    updateProduct,
    clearError,
    fetchAllProducts, // 2. Exporta la nueva funcin
  };

  return (
    <ProductsContext.Provider value={value}>
      {children}
    </ProductsContext.Provider>
  );
};
