import React, { useState, useMemo } from 'react';
import useCart from '../../context/useCart';
import { useNavigate } from 'react-router';
import styles from './Checkout.module.css';

const Checkout = () => {
  const { items: cart, totalCost: totalAmount, clearCart } = useCart();
  const [paymentMethod, setPaymentMethod] = useState('transferencia');
  const navigate = useNavigate();

  const formatoMoneda = new Intl.NumberFormat("es-AR", {
    style: "currency",
    currency: "ARS",
    maximumFractionDigits: 0,
  });

  const flatCart = useMemo(() => {
    return cart.flatMap(item =>
      item.selections.map(selection => {
        const finalPrice = item.discount > 0
          ? item.price * (1 - item.discount / 100)
          : item.price;
        
        return {
          ...item,
          size: selection.size,
          quantity: selection.quantity,
          finalPrice: finalPrice,
        };
      })
    );
  }, [cart]);

  const WHATSAPP_PHONE = "+5493774491008";

  const handleConfirmPurchase = () => {
    let message = "Hola! Quisiera confirmar mi pedido:\n\n";
    message += " *RESUMEN DE LA COMPRA* \n";
    
    flatCart.forEach(item => {
      message += ` ${item.name} (Talle: ${item.size}) - Cantidad: ${item.quantity}\n`;
    });

    message += `\n *Mtodo de pago elegido:*\n${paymentMethod === 'transferencia' ? 'Transferencia Bancaria' : 'Efectivo (presencial)'}\n\n`;
    message += "Espero la confirmacin para coordinar el pago y la entrega! Gracias! ";

    const url = `https://wa.me/${WHATSAPP_PHONE}?text=${encodeURIComponent(message)}`;
    
    window.open(url, "_blank");
    clearCart();
    navigate('/');
  };

  if (flatCart.length === 0) {
    return (
      <main className={styles.checkoutPage}>
        <div className={styles.emptyCart}>
          <h2>Tu carrito est vaco</h2>
          <p>No tienes productos para finalizar la compra.</p>
          <button onClick={() => navigate('/products')} className={styles.actionButton}>
            Ver Productos
          </button>
        </div>
      </main>
    );
  }

  return (
    <main className={styles.checkoutPage}>
      <div className={styles.checkoutLayout}>
        <div className={styles.summarySection}>
          <h1 className={styles.mainTitle}>Resumen de tu pedido</h1>
          <div className={styles.summaryList}>
            {flatCart.map(item => (
              <div key={`${item.sku}-${item.size}`} className={styles.productItem}>
                <img src={item.image} alt={item.name} className={styles.productImage} />
                <div className={styles.productDetails}>
                  <span className={styles.productName}>{item.name}</span>
                  <span className={styles.productMeta}>Talle: {item.size}</span>
                  <span className={styles.productMeta}>Cantidad: {item.quantity}</span>
                </div>
                <div className={styles.productPrice}>
                  <span>{formatoMoneda.format(item.finalPrice * item.quantity)}</span>
                </div>
              </div>
            ))}
          </div>
          <div className={styles.total}>
            <span>Total</span>
            <span>{formatoMoneda.format(totalAmount)}</span>
          </div>
        </div>

        <div className={styles.paymentSection}>
          <h2 className={styles.sectionTitle}>Elige tu mtodo de pago</h2>
          <div className={styles.radioGroup}>
            <label className={`${styles.radioLabel} ${paymentMethod === 'transferencia' ? styles.selected : ''}`}>
              <input
                type="radio"
                name="paymentMethod"
                value="transferencia"
                checked={paymentMethod === 'transferencia'}
                onChange={(e) => setPaymentMethod(e.target.value)}
              />
              <div className={styles.radioIcon}></div>
              <div className={styles.radioText}>
                <strong>Transferencia Bancaria</strong>
                <small>Te enviaremos los datos por WhatsApp.</small>
              </div>
            </label>
            <label className={`${styles.radioLabel} ${paymentMethod === 'efectivo' ? styles.selected : ''}`}>
              <input
                type="radio"
                name="paymentMethod"
                value="efectivo"
                checked={paymentMethod === 'efectivo'}
                onChange={(e) => setPaymentMethod(e.target.value)}
              />
              <div className={styles.radioIcon}></div>
              <div className={styles.radioText}>
                <strong>Efectivo (Presencial)</strong>
                <small>Coordina el pago al momento de la entrega.</small>
              </div>
            </label>
          </div>
          <div className={styles.actions}>
            <button onClick={handleConfirmPurchase} className={styles.actionButton}>
              Confirmar Pedido por WhatsApp
            </button>
            <button onClick={() => navigate('/products')} className={styles.secondaryButton}>
              Seguir Comprando
            </button>
          </div>
        </div>
      </div>
    </main>
  );
};

export default Checkout;